/*480px - 768px = smartphone and tablet*/
@media (min-width: 200px) and (max-width: 600px){
    .textoPrincipal h1{
        color: aqua;
        font-size: 14px;
        display: flex;
        align-items: center;
        justify-content: center;
        background-color: blue;
        width: 100vh;
    }
    .inner{
        width: 100%;
        
    }
    .carrossel{
        width: 100vh;

    }
}
/*laptops*/
@media (min-width: 768px){
    .textoPrincipal h1{
        color: aqua;
    }
}
/*1024px desktop*/
@media (min-width: 1024px){
    .textoPrincipal h1{
        color: aqua;
    }
}
/*maiores que 1200px, tvs*/
@media (min-width: 1200px){
    .textoPrincipal h1{
        color: aqua;
    }
}